
.logo-img{
    height: 110px;
    width: 110px;
}
.app{
    width: 100%;
    height: 100%;
}
.header{
    display: flex;
    width: 100%;
    justify-content: space-between;
    box-sizing: border-box;
    background-color: rgb(255, 255, 255);
    align-items: center;
    padding: 0px 70px;

}
.nav-ul{
    display: flex;
    list-style-type: none;
    padding-top: 0;
    /* margin-top: 20px; */
    font-size: 18px;
    padding-right: 200px;
    align-items: center;
    justify-content: center;
}
.nav-ul > li{
    padding: 10px 40px;
    margin: 8px;  
    color: rgb(36, 36, 36);
}
.search{
    padding: 10px;
}
.res-card{
    width: 200px;
    height: 380px;
    /* border: 1px solid black; */
    border-radius: 10px;
    background-color: #f0f0f0;
    margin: 5px;
}
.res-card:hover{
    border: 2px solid black;
}
.res-sample{
    width: 100%;
    height: 50%;
    border-radius: 10px;
}
.res-card > h3{
    
    margin-bottom: 0;
    padding-left: 5px;
}
.res-card > h4{
    margin-top: 0px;
    margin-bottom: 5px;
    padding-left: 5px;
}
.res-container{
    display: flex;
    flex-wrap: wrap;
}